.homePage {
  overflow: hidden;

  // btnArea 共用樣式
  .btnArea {
    display: flex;

    button {
      display: flex;

      &.btnSecondary {
        margin-right: 20px;
      }
    }

    img {
      width: 18px;
      height: 18px;
      margin-left: 8px;
    }
  }

  // 產品區塊共用樣式
  .titleArea {
    padding-top: 60px;
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-wrap: wrap;
    }

    .title {
      
      p {
        line-height: 24px;

        @include mobile {
          font-size: $fontSizeDefault - $fontSpace;
        }
      }
      h5 {
        color: $primary;
        line-height: 36px;
        font-weight: bold;
        padding-top: 2px;

        @include mobile {
          font-size: $fontSizeDefault + $fontSpace * 3;
        }
      }
    }

    .btnArea {
      align-self: flex-end;

      @include mobile {
        width: 100%;
        padding-top: 16px;
      }

      .button,.btnSecondary {
        display: flex;

        @include mobile {
          width: 100%;
          justify-content: center;
          margin-right: -20px;
        }

        img {
          padding-left: 8px;
        }
      }
    }
  }

  // hero
  .hero {
    padding-top: 104px;
    background-color: $bgc;
    height: 100vh;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;


    // 背景
    .backgroundImg {
      position: absolute;
      bottom: 0px;
      right: 10px;
      z-index: 500;

      img {
        width: 100%;
        height: 100%;
      }

      @include laptop {
        right: -196px;
        bottom: 0px;
      }
      @include pad {
        display: none;
      }
      @include mobile {
        display: none;
      }
    }
    .img1,.img2,.img3,.img4,.img5,.img6,.img7,.img8,.img9 {
      position: absolute;
    }
    .img1 {
      bottom: 0px;
      right: 133px;
      
      @include laptop {
        right: -73px;
      }
      @include pad {
        display: none;
      }
      @include mobile {
        display: none;
      }
    }
    .img2{
      bottom: 0px;
      right: 37px;

      @include laptop {
        right: -169px;
      }
      @include pad {
        display: none;
      }
      @include mobile {
        display: none;
      }
    }
    .img3 {
      top: 147px;
      left: 115px;
    }
    .img5 {
      top: 155px;
      left: 720px;
    }
    .img6 {
      top: 200px;
      left: 680px;
    }
    .img7 {
      top: 210px;
      left: 680px;
    }
    .img8 {
      bottom: -3.4px;
      left: -84px;
    }
    .img9 {
      top: 0;
    }

    // 標題
    h1,h3{
      font-weight: bold;
      color: $primary;
    }
    h1 {
      padding-top: 48px;
      line-height: 68px;

      @include mobile {
        font-size: 46px;
        line-height: 60px;
      }
    }
    h3 {
      padding-top: 4px;
      line-height: 60px;

      @include mobile {
        font-size: 28px;
        line-height: 38px;
      }
    }
    p {
      line-height: 24px;
      display: inline-block;
      padding-top: 24px;
      width: 480px;

      @include mobile {
        font-size: 12px;
        line-height: 18px;
        width: 100%;
      }
    }

    // btn
    .btnArea {
      padding-top: 34px;
    }
  }

  // banner
  .bannerArea01,.bannerArea02 {
    padding-top: 36px;
    @include pad {
      display: none;
    }
    
    .banner {

      img {
        width: 100%;

        &.banner01 {
          @include pad {
            display: none;
          }
        }
        &.banner01Mobile {
          display: none;

          @include pad {
            display: block;
          }
        }
      }
    }
    .banner01,.banner02 {

      &.row {
        padding-top: 64px;
        position: absolute;
        top: 20px;
        width: 100%;
        &.l-block {
          justify-content: flex-start;
          padding-left: 40px;
        }
        &.r-block {
          justify-content: flex-end;
        }
        @include laptop {
        padding-top: 24px;
        }
        @include pad {
          padding-top: 64px;
        }

        .mr {
          margin-right: 30px;
          @include laptop {
            margin-right: 0;
          }
        }

        .textArea {
          @include laptop {
            text-align: center;
          }

          h2 {
            line-height: 68px;
            color: $primary;
            @include laptop {
              font-size: $fontSizeDefault + $fontSpace * 5;
              line-height: 36px;
            }
          }
          h4 {
            line-height: 54px;
            font-weight: bold;
            color: $primary;
            @include laptop {
              font-size: $fontSizeDefault + $fontSpace * 3; 
              line-height: 36px;
            }
          }
          span {
            font-size: 12px;
            line-height: 18px;
            color: $neutral-80;
            display: inline-block;
            width: 394px;
            text-align: end;
            padding-top: 8px;
            @include laptop {
              width: 260px;
              text-align: center;
            }
          }
        }

        .btnArea {
          padding-top: 24px;
          justify-content: center;
        }
      }
    }
    .banner02 {
      &.row {
        .textArea {
          h4 {
            width: 466px;
          }
          span {
            text-align: start;
          }
        }
      }
    }
  }

  // brands
  .brandsArea {
    @include pad {
      display: none;
    }

    .titleArea {

      .title {
        display: flex;
        align-items: center;

        p {
          line-height: 31px;
        }
        h5 {
          line-height: 36px;
          padding-left: 8px;
        }
      }
    }

    .brandsImg {
      padding-top: 16px;
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
    }
  }

  // footer
  .footer {
    background-color: $bgc;
    padding-top: 80px;
    overflow: hidden;  // for margin塌陷
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;

    .subscribeBlock {
      background-color: #003459 ;
      padding: 32px;
      border-radius: 14px;

      h5 {
        line-height: 36px;
        font-weight: bold;
        color: $neutral-00;
      }

      .inputArea {
        background-color: $neutral-00;
        border-radius: 8px;
        padding: 12px;
        @include pad {
          margin-top: 16px;
        }

        .inputStyle {
          width: 100%;
          height: 48px;
          border-radius: 8px;
          border: 1px solid $neutral-40;
          padding-left: 24px;
          @include pad {
            margin-bottom: 12px;
          }
        }
        .btnStyle {
          width: 100%;
          border-radius: 8px;
          padding: 12px 31px;
          background-color: #003459;
          color: $neutral-00;
        }
      }
    }

    .footerContent {
      margin: 40px 0;
      display: flex;
      justify-content: space-between;
      @include pad {
        flex-direction: column;
        align-items: center;
        padding: 0;
      }

      .footerNav {
        
        a {
          width: 80px;
          display: inline-block;
          text-align: center;
        }
      }
      .footerIcon {
        @include pad {
          padding-top: 20px;
        }
        img {
          margin: 0 20px;
        }
      }
    }

    .footerCopyright {
      border-top: 1px solid $neutral-40;
      padding: 40px 0;
      display: flex;
      justify-content: space-between;
      @include pad {
        flex-direction: column;
        align-items: center;
      }

      span {
        color: $neutral-60;
        line-height: 20px;
      }
      .copyrightInfo {
        @include pad {
          order: 2;
          padding-top: 32px;
        }
      }
      .footerLogo {
        @include pad {
          order: 1;
        }
      }
      .policyInfo {
        @include pad {
          order: 3;
          padding-top: 8px;
        }
        span:first-child {
          display: inline-block;
          padding-right: 16px;
        }
      }
    }
  }
}